import DroppableLetter from "./DroppableLetter";
import Float from "./FloatMain";
import FloatUtil from "./FloatUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DragableLetter extends cc.Component {

    private initPos: cc.Vec2 = cc.Vec2.ZERO
    private currPos: cc.Vec2 = cc.Vec2.ZERO

    dropArea: DroppableLetter[] = []

    public letter = ''

    static poses = [cc.v2(-250, 93.153), cc.v2(-150, 93.153), cc.v2(-50, 93.153), cc.v2(50, 93.153), cc.v2(150, 93.153), cc.v2(250, 93.153)]

    freezed = false
    @property(cc.Vec2)
    lastPos: cc.Vec2 = cc.Vec2.ZERO

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchBegan, this)
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this)
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this)
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this)
        this.dropArea.push(cc.find('btn_c', this.node.parent).getComponent(DroppableLetter))
        this.dropArea.push(cc.find('btn_d', this.node.parent).getComponent(DroppableLetter))
        this.letter = cc.find('l', this.node).getComponent(cc.Label).string
    }

    private onTouchBegan(event: cc.Event.EventTouch) {
        if (!Float.instance.canDrag) {
            return
        }
        if (this.freezed) {
            return
        }
        Float.instance.dragged = true
        Float.instance.hand.hide()
        this.currPos.x = this.node.x
        this.currPos.y = this.node.y
        this.initPos.x = this.node.x
        this.initPos.y = this.node.y
    }

    private onTouchMove(event: cc.Event.EventTouch) {
        if (!Float.instance.canDrag) {
            return
        }
        if (this.freezed) {
            return
        }
        let deltaX = event.getDeltaX()
        let deltaY = event.getDeltaY()
        this.currPos.x += deltaX
        this.currPos.y += deltaY
        this.node.x = this.currPos.x
        this.node.y = this.currPos.y
        // let worldPos = this.node.parent.convertToWorldSpaceAR(this.node.position)
    }

    private async onTouchEnd(event: cc.Event.EventTouch) {
        if (!Float.instance.canDrag) {
            return
        }
        if (this.freezed) {
            return
        }
        // console.log(`touch x: ${event.getLocationX()} y: ${event.getLocationY()}`)
        let currX = event.getLocationX()
        let currY = event.getLocationY()

        // let dis = Math.sqrt((currX - this.initPos.x) * (currX - this.initPos.x) + (currY - this.initPos.y) * (currY - this.initPos.y))
        // if (dis > 20) {
        // } else {
        // }
        for (let i = 0; i < this.dropArea.length; i++) {
            const element = this.dropArea[i];
            let pos = FloatUtil.convertToNodeSpace(this.node.parent, element.node.parent, this.node.x, this.node.y)
            let box = element.node.getBoundingBox()
            if (box.contains(pos)) {
                if (this.letter == element.letter) {
                    if (this.letter == 'C') {
                        Float.instance.playVoice('Big C')
                    } else if (this.letter == 'c') {
                        Float.instance.playVoice('Small c')
                    } else if (this.letter == 'D') {
                        Float.instance.playVoice('Big D')
                    } else if (this.letter == 'd') {
                        Float.instance.playVoice('Small d')
                    }
                    element.put(this)
                    return
                } else {
                    Float.instance.playVoice('wrong')
                    Float.instance.wrongNum++
                    await Float.instance.shakeBtnByNode(this.node)
                }
            }
        }
        //
        this.goBack()
    }



    async goBack() {
        return new Promise<void>(resolve => {
            cc.tween(this.node).to(0.3, { x: this.lastPos.x, y: this.lastPos.y }).call(() => {
                resolve()
            }).start()
        })
    }
}

