export default class FloatUtil {
    static convertToNodeSpace(src: cc.Node, dest: cc.Node, xy: cc.Vec2 | number, y?: number) {
        let worldPos: cc.Vec2
        if (typeof xy == 'object') {
            worldPos = src.convertToWorldSpaceAR(xy)
        } else {
            worldPos = src.convertToWorldSpaceAR(cc.v2(xy, y))
        }
        let nodePos = dest.convertToNodeSpaceAR(worldPos)
        return nodePos
    }
}