import Tigo from "../../common_en/Tigo";
import DragableLetter from "./DragableLetter";
import Float from "./FloatMain";
import FloatUtil from "./FloatUtil";
import Unit from "./Unit";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DroppableLetter extends cc.Component {

    poses: cc.Vec2[] = [cc.v2(0, 37), cc.v2(-45, -31), cc.v2(45, -31)]
    usages: number[] = [0, 0, 0]

    other: DroppableLetter = null

    async put(letter: DragableLetter) {
        for (let i = 0; i < this.usages.length; i++) {
            const element = this.usages[i]
            if (element == 0) {
                let pos = FloatUtil.convertToNodeSpace(this.node, this.node.parent, this.poses[i])
                letter.freezed = true
                await this.moveTo(letter, pos)
                this.usages[i] = 1
                if (this.isCompleted()) {
                    let pp = Float.instance
                    pp.canDrag = false
                    if (pp.currResolve) {
                        const cb = pp.currResolve
                        pp.currResolve = null
                        console.log('answer is right, continue>>>>>')
                        
                        // await pp.tigo.play(Unit.TIGO_HAPPY)
                        let stars = 3
                        let inst = Float.instance
                        if (inst.wrongNum == 0) {
                            stars = 3
                        } else if (inst.wrongNum == 1) {
                            stars = 2
                        } else {
                            stars = 1
                        }
                        inst.total_star += stars
                        Tigo.callAppToCollect(inst.gid, 1004, stars);
                        await inst.sleep(3)
                        Float.instance.playVoice('right')
                        await inst.coin_board.setCoinAni(stars)
                        await inst.sleep(1)
                        cb()
                    }
                }
                break
            }
        }
    }

    isCompleted() {
        return this.isFull() && this.other.isFull()
    }

    isFull() {
        let ret = true
        for (let i = 0; i < this.usages.length; i++) {
            const element = this.usages[i]
            if (element == 0) {
                ret = false
                break
            }
        }
        return ret
    }

    async moveTo(letter: DragableLetter, pos: cc.Vec2) {
        return new Promise<void>(resolve => {
            cc.tween(letter.node).to(0.2, { x: pos.x, y: pos.y }).call(() => {
                resolve()
            }).start()
        })
    }

    reset() {
        for (let i = 0; i < this.usages.length; i++) {
            this.usages[i] = 0
        }
    }

    letter: string = ''
    onLoad() {

    }

    start() {

    }

    // update (dt) {}
}
