import AudioManager from "../../../kit/system/audio/AudioManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Unit extends cc.Component {

    @property({ type: sp.Skeleton, displayName: 'spine' })
    spine: sp.Skeleton = null


    @property({ type: cc.Node, displayName: '' })
    anchor: cc.Node = null

    vine: sp.spine.Attachment = null

    @property({ displayName: '' })
    spine_name = ''

    static NAME_TIGO = 'tigo'

    static TIGO_HAPPY = 'happy'
    static TIGO_IDLE = 'idle'
    static TIGO_JUMP = 'jump'
    static TIGO_MOVE = 'move'
    static TIGO_SCRATCH = 'naotou'
    static TIGO_SHOOT1 = 'shoot1'
    static TIGO_SHOOT2 = 'shoot2'
    static TIGO_SHOOT3 = 'shoot3'
    static TIGO_SURPRISE = 'surprise'
    static TIGO_WALK = 'walk'
    static TIGO_GRAB = 'zhua'
    static MONSTER1_IDLE = 'daiji'
    static MONSTER1_HITTED = 'hit'
    static MONSTER1_DISAPPEAR = 'xiaoshi'
    static MONSTER2_IDLE = 'daiji'
    static MONSTER2_HITTED = 'beijida'
    static MONSTER2_DISAPPEAR = 'xiaoshi'
    static MONSTER3_IDLE = 'daiji_idle'
    static MONSTER3_HITTED = 'shouji_idle'
    static MONSTER3_DISAPPEAR = 'xiaoshi_idle'
    static SNAIL_IDLE = 'idle'
    static SNAIL_HAPPY = 'happy'
    static SNAIL_SWIM = 'swim'

    static BOX_CLOSED = 'idle2'
    static BOX_OPENING = 'open'
    static BOX_OPENED = 'idle1'

    static JUMP_DUARATION = 1

    onLoad() {
        if (this.spine_name == Unit.NAME_TIGO) {//tigo
            let slot = this.spine.findSlot('tengtiao') as sp.spine.Slot
            this.vine = slot.getAttachment()
            this.showVine(false)
        }
        this.showVine(false)
        this.spine.setCompleteListener(() => {
            if (this.currResolve) {
                const cb = this.currResolve
                this.currResolve = null
                cb()
            }
        })
    }

    start() {
    }

    private currResolve: Function = null

    async play(name: string, loop = false) {
        return new Promise<void>((resolve) => {
            this.spine.setAnimation(0, name, loop)
            this.currResolve = resolve
        })
    }

    async jumpTo(dest: cc.Node | cc.Vec2) {
        AudioManager.playEffect('Float','sound/SE_qihuanpiaoliu_09')
        return new Promise<void>(async (resolve) => {
            cc.tween(this.node).delay(0.3).to(0.4, { x: dest.x, y: dest.y }, { easing: 'sineInOut' }).start()
            await this.play(Unit.TIGO_JUMP, false)
            resolve()
        })
    }

    static walkSpeed = 400//250 // px/s
    async walkTo(dest: cc.Node | cc.Vec2) {
        AudioManager.playLoopEffect('Float','sound/SE_qihuanpiaoliu_06')
        let duration = (dest.x - this.node.x) / Unit.walkSpeed
        return new Promise<void>((resolve) => {
            this.play(Unit.TIGO_WALK, true)
            cc.tween(this.node).delay(0.1).to(duration, { x: dest.x }).call(() => {
                AudioManager.stopLoopEffect('sound/SE_qihuanpiaoliu_06')
                resolve()
            }).start()
        })
    }

    showVine(b: boolean) {
        if (this.spine_name == Unit.NAME_TIGO) {//tigo
            let slot = this.spine.findSlot('tengtiao') as sp.spine.Slot
            if (b) {
                slot.setAttachment(this.vine)
            } else {
                slot.setAttachment(null)
            }
        }
    }

}
